

html, body, #root, .App {
  width: 100%;
  height: 100%;
  background-color: lightgrey;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.App, .ProjectTitle * {
  font-size: 10px;
  line-height: 12px;
}

.App.live, .App.live .ProjectTitle * {
  font-size: 12px;
  line-height: 16px;
}

.App .live-only {
  display: none;
}

.App.live .live-only {
  display: block;
}

.download-nav {
  text-align: center;
  margin: 24px;
  font-size: 18px;
}


.IconNav svg, .download-nav svg {
  font-size: 24px;
  margin-right: 8px;
}

.contact-ul {
  line-height: 16px;
  margin-top: 0;
  margin-bottom: 0;
}


.IconNav .contact-ul li {
   display: inline-block;
}

.IconNav {
  width: 100%;
  text-align: center;
}

.App.live .footer {
  margin-bottom: 20px;
  margin-top: 10px;
}

.IconNav svg {
   margin-right: 0;
   padding: 8px;
}


.App.live .contact-ul, .App.live .date {
  font-size: 12px;

}

.App.live .contact-ul {
  line-height: 22px;
}




/* Grid */

.cv-pdf {
  height: 792px;
  width: 612px;
  background-color: white;
  margin: 0 auto;
  padding: 36px 36px 56px 36px;
}

.App.live .cv-pdf {
  margin-top: 20px;
  height: auto;
  padding-bottom: 10px;
  position: relative;
}



.grid-template {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 100px 1fr;
  grid-template-areas: 'contact-lft contact-rt' 'col-one col-two' ;
  grid-gap: 24px;
}


.App.live .grid-template {
  grid-template-rows: 140px 1fr;
  grid-gap: 28px;
}


.contact-lft {
  grid-area: contact-lft;
}

.contact-rt {
  grid-area: contact-rt;
}


.contact > p {
  margin-top: 0;
}

.intro-p {
  margin-top: 0;
  font-weight: bold;
  font-style: italic;
}

.col-one {
  grid-area: col-one;
  max-width: 100%;
}


.col-two {
  grid-area: col-two;
}


@media screen and (max-width: 612px) {
  .App.live .grid-template {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
    grid-template-areas: 'contact-lft' 'contact-rt' 'col-one' 'col-two';
    grid-gap: 0;
  }

  .App.live .cv-pdf {
    height: auto;
    width: auto;
    margin-top: 0;
  }

  .App.live .cv-pdf:nth-of-type(2) {
    padding-top: 0px;
  }


  .App.live .live-only-large {
    display: none;
  }
}

/* end of grid */
h2, h3 {
  margin-top: 0;
}

a {
  text-decoration: none;
  color: orangered;
  font-weight: bold;
}

section {
  margin-bottom: 28px;
}

.App.live section {
  margin-bottom: 40px;
}

ul {
  list-style: none;
  padding-left: 0;
}

ul.section-list  li {
  margin-bottom: 4px;
  padding-left: 16px;
}

.App.live ul.section-list  li {
  margin-bottom: 8px;
  padding-left: 16px;
}


p.project-stack {
  font-weight: bold;
}

.SectionTitle {
  font-size: 14px;
}

.App.live .SectionTitle:not(.date) {
  font-size: 18px;
}

.SectionTitle.date {
  color: gray;
   font-size: 10px;
   margin-bottom: 0;
}


div.experience, div.project {
  margin-bottom: 16px;
}

.App.live div.experience, div.project {
  margin-bottom: 24px;
}


.role {
  color: gray;
}

.inline-circle-img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  float: left;
  shape-outside: circle();
  margin-right: 24px;
  margin-bottom: 0px;
  transform: rotate(90deg);

}


.App span:not(.role) {
  display: inline-block;
  margin-right: 8px;
}




